<template>
  <div class="registerInfo">
    <div class="input">
      <span>用户名:</span>
      <el-input style="width:300px;" v-model="registerInfo[0].username"></el-input>
    </div>
    <div class="input">
      <span>密码:</span>
      <el-input type="password" style="width:300px;" v-model="registerInfo[1].password"></el-input>
    </div>
    <div class="input">
      <span>密码确认:</span>
      <el-input type="password" style="width:300px;" v-model="registerInfo[2].surePassword"></el-input>
    </div>
    <div class="input">
      <span>联系人:</span>
      <el-input style="width:300px;" v-model="registerInfo[3].man"></el-input>
    </div>
    <div class="input">
      <span>联系电话:</span>
      <el-input style="width:300px;" v-model="registerInfo[4].phone"></el-input>
    </div>
    <span style="color:red;margin-left:15px;">以上内容都为必填项</span>
    <br>
    <el-button style="float:right;margin-right:15px;width:200px;" type="primary" @click="next()">下一步</el-button>
  </div>
</template>

<script>
import axios from 'axios'
// import tipInput from '@/components/TipInput/tipInput'
export default {
  data () {
    return {
      registerInfo:[
          {name:"username",username:"",message:"请输入用户名"},
          {name:"password",password:"",message:"请输入密码"},
          {name:"surePassword",surePassword:"",message:"请确认密码"},
          {name:"man",man:"",message:"请输入联系人"},
          {name:"phone",phone:"",message:"请输入联系电话"}
      ]
    }
  },
  created () {
    
  },
  methods:{
      next() {
          var that = this
          var flag = true
          for(var i =0 ;i<that.registerInfo.length;i++){
            if(!that.registerInfo[i][that.registerInfo[i].name]){
              this.$message({
                message:that.registerInfo[i].message ,
                type: 'warning'
              });
              flag = false
              break
            }
          }
          if(flag){
            that.$router.push({path:'/Register/taobao'})
          }
      }
    }
}
</script>

<style lang="scss" scoped>
.registerInfo{
    margin-top: 20px;
    width: 430px;
    .input{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 15px;
    }
} 
</style>
<style>
.headerWrapper .el-dropdown{
  color: #000;
  font-size: 16px;
}
</style>



